.page--user-dashboard {
  width: 100%;
  height: 100%;
  background-color: #f6f6f6;
  display: flex;
  padding: 24px;
  box-sizing: border-box; }
  .page--user-dashboard .aside__avator {
    position: relative; }
  .page--user-dashboard .icon-edit {
    font-size: 14px;
    position: absolute;
    top: 20px;
    right: 20px; }
  .page--user-dashboard .tag {
    margin-left: 16px;
    border-radius: 9px;
    border: 1px solid;
    font-size: 12px;
    padding: 0 4px; }
  .page--user-dashboard .card {
    background: #fff;
    height: 352px;
    width: 100%; }
    .page--user-dashboard .card__header {
      line-height: 48px;
      height: 48px;
      border-bottom: 1px solid #e6e7e8;
      padding: 0 24px;
      font-size: 16px; }
    .page--user-dashboard .card__content {
      padding: 24px;
      display: flex;
      flex: 1;
      flex-direction: column; }
  .page--user-dashboard .user-dashboard__aside {
    flex: 0 0 216px;
    margin-right: 24px;
    height: 100%;
    background-color: #fff; }
    .page--user-dashboard .user-dashboard__aside .aside__avator {
      height: 216px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; }
    .page--user-dashboard .user-dashboard__aside .aside__section {
      margin-top: 12px;
      padding-left: 24px; }
      .page--user-dashboard .user-dashboard__aside .aside__section .title {
        height: 48px;
        line-height: 48px;
        font-size: 16px; }
      .page--user-dashboard .user-dashboard__aside .aside__section .content-item {
        display: flex;
        margin-bottom: 8px;
        line-height: 22px; }
      .page--user-dashboard .user-dashboard__aside .aside__section > .content-item:last-of-type {
        margin-bottom: 0; }
      .page--user-dashboard .user-dashboard__aside .aside__section .content-item--left {
        color: #999;
        flex: 0 0 56px;
        margin-right: 16px; }
      .page--user-dashboard .user-dashboard__aside .aside__section .content-item--right {
        flex: 1; }
