.page--list-embeded {
  box-sizing: border-box; }
  .page--list-embeded .page--list-header {
    height: 56px;
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    align-items: center;
    font-weight: 500;
    padding: 24px;
    box-sizing: border-box;
    margin-bottom: 8px; }
    .page--list-embeded .page--list-header .search-btn {
      padding: 0 5px; }
    .page--list-embeded .page--list-header .icon-search {
      color: #4285f4;
      font-size: 20px; }
    .page--list-embeded .page--list-header .creat-btn {
      margin-left: 16px;
      height: 30px; }
  .page--list-embeded .hi-dropdown {
    width: 100px;
    padding: 8px;
    font-size: 18px;
    margin-bottom: 8px; }
  .page--list-embeded .list {
    box-sizing: border-box;
    padding: 16px 24px;
    background: #fff;
    margin: 0 24px; }
    .page--list-embeded .list .list-item__header {
      display: flex;
      align-items: center;
      font-size: 14px; }
    .page--list-embeded .list .list-item__info {
      color: #666;
      display: flex;
      padding-top: 2px; }
    .page--list-embeded .list .tag {
      margin-left: 16px;
      font-size: 12px; }
  .page--list-embeded .list-item {
    border-bottom: 1px solid #f6f6f6;
    padding: 20px 0; }
    .page--list-embeded .list-item__content {
      display: flex; }
    .page--list-embeded .list-item__separation {
      color: #e7e7e7; }
  .page--list-embeded .list-item:first-child {
    padding: 0 0 20px 0; }
