  background: #fff;
  margin: 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--form-double-column .hi-form-item {
    padding: 0 24px; }
  .page--form-double-column .hi-form__title {
    font-size: 16px;
    height: 47px;
    line-height: 47px;
    padding: 0 24px;
    border-bottom: 1px solid #e7e7e7;
    width: 100%; }
  .page--form-double-column .photo-display {
    padding: 0; }
