.page--list-indicator {
  box-sizing: border-box; }
  .page--list-indicator .indicator__container {
    padding: 16px 24px; }
  .page--list-indicator .indicator——type_container {
    display: flex;
    padding-bottom: 16px; }
  .page--list-indicator .indicator——type_item {
    height: 36px;
    width: 96px;
    border: 1px solid #e7e7e7;
    border-radius: 18px;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 40px;
    background: #fff; }
    .page--list-indicator .indicator——type_item:hover {
      color: #4285f4;
      border-color: #4285f4; }
  .page--list-indicator .indicator——item_header {
    padding-left: 11px;
    font-size: 16px;
    margin: 12px 0;
    position: relative; }
    .page--list-indicator .indicator——item_header::before {
      content: '';
      position: absolute;
      height: 15px;
      width: 3px;
      left: 0;
      top: 4.5px;
      background: #4285f4; }
  .page--list-indicator .page--list-header {
    height: 56px;
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    align-items: center;
    font-weight: 500;
    padding: 24px;
    box-sizing: border-box; }
    .page--list-indicator .page--list-header .search-btn {
      padding: 0 5px; }
    .page--list-indicator .page--list-header .icon-search {
      color: #4285f4;
      font-size: 20px; }
