.page--dashboard {
  padding: 20px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--dashboard .dashboard-title {
    font-size: 18px;
    font-weight: 600; }
  .page--dashboard .tag-btn {
    display: inline-block;
    width: 80px;
    height: 36px;
    border-radius: 18px;
    line-height: 36px;
    text-align: center;
    font-size: 14px;
    color: #333;
    margin-right: 24px;
    border: 1px solid #ccc;
    background: #fff;
    cursor: pointer; }
    .page--dashboard .tag-btn:hover {
      border-color: #4285f4;
      color: #4285f4; }
    .page--dashboard .tag-btn.active {
      border-color: #4285f4;
      background: #4285f4;
      color: #fff; }
  .page--dashboard .info {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 96px;
    width: 100%;
    background: #fff; }
    .page--dashboard .info__amount {
      margin: 20px 0 0 0;
      font-size: 20px;
      font-weight: 500;
      color: #4285f4; }
    .page--dashboard .info__name {
      margin-top: 6px;
      color: #333; }
  .hi-grid__col-12 .page--dashboard .card {
    flex-basis: 280px; }
  .hi-grid__col-8 .page--dashboard .card {
    flex-basis: 164px;
    height: 164px; }
  .page--dashboard .card .hi-table {
    width: 100%; }
  .page--dashboard .card__canvas {
    box-sizing: border-box;
    width: 100%; }
