.page--detail-card {
  padding: 0 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--detail-card .detail-card__header .row-01 {
    color: #ccc;
    height: 32px;
    align-items: center; }
  .page--detail-card .detail-card__header .row .spacer {
    margin: 0 10px; }
  .page--detail-card .detail-card__header .row-02 {
    color: #383e47;
    margin-top: 0;
    align-items: center;
    height: 52px;
    padding: 0;
    margin-bottom: 12px; }
  .page--detail-card .detail-card__header .row-02 h3 {
    margin: 0 0 4px 0; }
  .page--detail-card .detail-card__header .row-03 {
    padding-top: 4px;
    font-size: 12px;
    color: #999;
    margin-bottom: 18px; }
  .page--detail-card .detail-card__header .row-03 .spacer {
    margin: 0 20px;
    color: #e7e7e7; }
  .page--detail-card .detail-card__header .row-03 .spacer:last-child {
    display: none; }
  .page--detail-card .detail-card__card {
    box-sizing: border-box;
    background: #fff; }
    .page--detail-card .detail-card__card .title {
      font-size: 16px;
      border-bottom: 1px solid #e7e7e7;
      padding: 0 24px 12px;
      margin: -12px -24px 0 -24px; }
      .page--detail-card .detail-card__card .title:not(:first-child) {
        margin-top: 32px; }
    .page--detail-card .detail-card__card ul {
      list-style: none;
      padding: 0;
      display: flex;
      flex-wrap: wrap; }
    .page--detail-card .detail-card__card li {
      display: flex;
      margin-top: 8px; }
    .page--detail-card .detail-card__card--base {
      width: calc(100% - 384px - 24px); }
      .page--detail-card .detail-card__card--base li {
        width: 50%; }
        .page--detail-card .detail-card__card--base li div:first-child {
          width: 110px;
          height: 100%;
          flex-shrink: 0;
          color: #999; }
        .page--detail-card .detail-card__card--base li div:last-child {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-break: keep-all;
          width: calc(100% - 150px);
          line-height: 22px; }
    .page--detail-card .detail-card__card--record {
      width: 384px;
      flex-shrink: 0;
      margin-left: 24px; }
      .page--detail-card .detail-card__card--record .hi-timeline {
        margin-top: 24px; }
    .page--detail-card .detail-card__card--stepper {
      margin-top: 24px; }
      .page--detail-card .detail-card__card--stepper .stepper {
        padding-top: 24px; }
      .page--detail-card .detail-card__card--stepper .form-title {
        font-size: 16px;
        justify-content: center;
        padding: 24px 0 12px; }
  .page--detail-card .detail-group__card {
    margin-top: 24px;
    background: #fff;
    padding: 0; }
    .page--detail-card .detail-group__card .hi-navmenu__list {
      padding: 0 24px !important; }
