.page--search {
  padding: 24px;
  color: #fff;
  min-height: calc(100vh - 64px);
  box-sizing: border-box;
  background-color: #333;
  background-size: cover;
  background-repeat: no-repeat; }
  .page--search .search-box {
    width: 500px;
    margin: 152px auto 59px auto; }
    .page--search .search-box .search-guide {
      font-size: 24px;
      color: #fff;
      margin: 0 0 24px 0;
      font-weight: 400; }
    .page--search .search-box .search-input {
      margin-top: 0; }
      .page--search .search-box .search-input .hi-input {
        height: 42px; }
      .page--search .search-box .search-input .hi-input__inner {
        border: none; }
      .page--search .search-box .search-input .search-btn {
        border: none;
        position: relative;
        height: 42px;
        color: #4285f4;
        font-size: 20px;
        border-radius: 0; }
      .page--search .search-box .search-input .search-btn::after {
        position: absolute;
        content: '';
        display: block;
        width: 1px;
        height: 20px;
        left: 0;
        top: 11px;
        background: #e7e7e7; }
    .page--search .search-box .search-hot {
      margin-right: 24px;
      cursor: pointer;
      font-size: 14px; }
  .page--search .tab-box {
    max-width: 1100px;
    margin: 0 auto; }
    .page--search .tab-box .tab-content {
      padding-top: 20px; }
      .page--search .tab-box .tab-content .hi-card {
        height: 120px;
        background: #fff;
        box-sizing: border-box;
        margin: 12px 0;
        border: none; }
      .page--search .tab-box .tab-content .hi-card p {
        margin: 0;
        color: #666;
        font-size: 14px; }
      .page--search .tab-box .tab-content .hi-card .short--cut__title {
        color: #333;
        margin-bottom: 12px;
        font-size: 16px; }
    .page--search .tab-box .hi-navmenu__list {
      border-bottom: none; }
      .page--search .tab-box .hi-navmenu__list .hi-navmenu__item {
        color: #fff;
        opacity: 0.75; }
      .page--search .tab-box .hi-navmenu__list li.on {
        opacity: 1; }
      .page--search .tab-box .hi-navmenu__list li.on::after {
        background: #fff; }
    .page--search .tab-box .mine-tab {
      flex-wrap: wrap; }
      .page--search .tab-box .mine-tab .hi-card__content {
        display: flex;
        height: 100%;
        align-items: center; }
      .page--search .tab-box .mine-tab .mine-info-icon {
        width: 46px;
        height: 46px;
        margin-right: 24px; }
