.page--detail-group {
  padding: 0 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--detail-group .detail-group__header .row-01 {
    color: #ccc;
    height: 32px;
    align-items: center; }
  .page--detail-group .detail-group__header .row .spacer {
    margin: 0 10px; }
  .page--detail-group .detail-group__header .row-02 {
    color: #383e47;
    margin-top: 0;
    align-items: center;
    height: 52px;
    padding: 0;
    margin-bottom: 12px; }
  .page--detail-group .detail-group__header .row-02 h2 {
    margin: 4px 0; }
  .page--detail-group .page--gutter {
    padding: 24px 24px 0 24px; }
  .page--detail-group .detail-group__card {
    background: #fff; }
    .page--detail-group .detail-group__card:not(:nth-child(2)) {
      margin-top: 24px; }
    .page--detail-group .detail-group__card .title {
      font-size: 16px;
      border-bottom: 1px solid #e7e7e7;
      padding: 0 24px 12px;
      margin: -12px -24px 0 -24px; }
    .page--detail-group .detail-group__card .title:not(:first-child) {
      margin-top: 32px; }
    .page--detail-group .detail-group__card ul {
      list-style: none;
      display: flex;
      flex-wrap: wrap;
      margin: 0;
      padding: 24px 0; }
    .page--detail-group .detail-group__card li {
      display: flex; }
  .page--detail-group .detail-group .detail-group__card--base li {
    width: 33%; }
  .page--detail-group .detail-group .detail-group__card--base li div:first-child {
    width: 110px;
    height: 100%;
    flex-shrink: 0;
    color: #999; }
  .page--detail-group .detail-group .detail-group__card--base li div:last-child {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: keep-all;
    width: calc(100% - 150px);
    line-height: 22px; }
  .page--detail-group .detail-group .detail-group__card--express .card-list {
    display: flex; }
  .page--detail-group .detail-group .detail-group__card--express .card-item {
    border: 1px solid #e7e7e7;
    padding: 24px;
    width: 376px;
    flex-direction: column; }
  .page--detail-group .detail-group .detail-group__card--express .card-item:not(:first-child) {
    margin-left: 24px; }
  .page--detail-group .detail-group .detail-group__card--express img {
    width: 80px;
    height: 80px;
    margin-right: 24px;
    flex-shrink: 0; }
  .page--detail-group .detail-group .detail-group__card--express .row-01 {
    font-size: 16px; }
  .page--detail-group .detail-group .detail-group__card--express .row-02 ul {
    flex-direction: column; }
  .page--detail-group .detail-group .detail-group__card--express .row-02 li {
    width: 100%; }
  .page--detail-group .detail-group .detail-group__card--express .row-02 div:first-child {
    width: 60px;
    margin-right: 16px;
    color: 16px;
    flex-shrink: 0; }
  .page--detail-group .detail-group__table {
    padding: 0;
    margin-bottom: 24px; }
    .page--detail-group .detail-group__table .hi-navmenu__list {
      padding: 0 24px !important; }
    .page--detail-group .detail-group__table .detail-group__table-container {
      padding: 24px; }
