#app aside > .hi-menu--vertical {
  overflow: auto; }

.card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  background: #fff;
  height: 100%;
  width: 100%; }
  .card__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 47px;
    line-height: 47px;
    border-bottom: 1px solid #e7e7e7;
    padding: 0 24px; }
  .card__title {
    font-size: 14px;
    font-weight: 400;
    color: #333; }
  .card__filter {
    display: flex; }
  .card__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 24px;
    width: 100%;
    height: 100%;
    box-sizing: border-box; }
  .card__footer {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    margin-top: 12px; }
