.page--portal {
  padding: 36px 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box;
  background-color: #f6f6f6; }

.portal__container {
  max-width: 1300px;
  margin: 0 auto; }
  .portal__container .icon-search {
    color: #4285f4;
    font-size: 20px; }

.portal__header {
  display: flex;
  align-items: center; }
  .portal__header h3 {
    margin-top: 0;
    margin-bottom: 12px;
    font-size: 16px;
    font-weight: 400; }

.portal__shortcut-list {
  flex-wrap: wrap;
  justify-content: space-between; }
  .portal__shortcut-list .hi-grid__col-8:nth-child(n+4) {
    margin-top: 24px; }

.portal__shortcut-item .hi-card {
  height: 120px;
  box-sizing: border-box;
  background-color: #fff;
  color: #666; }
  .portal__shortcut-item .hi-card h4 {
    margin-top: 0;
    margin-bottom: 12px;
    font-size: 16px;
    font-weight: 400;
    color: #333; }
  .portal__shortcut-item .hi-card p {
    margin: 0;
    font-size: 14px; }

.portal__todo-item {
  margin-bottom: 12px; }
  .portal__todo-item + .portal__todo-item {
    margin-top: 0; }

.portal-todo-item {
  justify-content: space-between;
  font-size: 14px;
  line-height: 22px; }
  .portal-todo-item__title {
    margin: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 22px; }
    .portal-todo-item__title,
    .portal-todo-item__title a {
      color: #333; }
  .portal-todo-item__desc, .portal-todo-item__date {
    font-size: 12px;
    line-height: 20px;
    color: #ccc; }
  .portal-todo-item__action {
    cursor: pointer; }
