.page--detail-stepper {
  padding: 0 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--detail-stepper .detail-stepper__header .row-01 {
    color: #ccc;
    height: 32px;
    align-items: center; }
  .page--detail-stepper .detail-stepper__header .row .spacer {
    margin: 0 10px; }
  .page--detail-stepper .detail-stepper__header .row-02 {
    color: #383e47;
    margin-top: 0;
    align-items: center;
    height: 52px;
    padding: 0;
    margin-bottom: 12px; }
  .page--detail-stepper .detail-stepper__header .row-02 h2 {
    margin: 4px 0; }
  .page--detail-stepper .detail-stepper__card {
    background: #fff; }
    .page--detail-stepper .detail-stepper__card:not(:nth-child(2)) {
      margin-top: 24px; }
    .page--detail-stepper .detail-stepper__card .title {
      font-size: 16px;
      border-bottom: 1px solid #e7e7e7;
      padding: 0 24px 12px;
      margin: -12px -24px 0 -24px; }
      .page--detail-stepper .detail-stepper__card .title:not(:first-child) {
        margin-top: 32px; }
    .page--detail-stepper .detail-stepper__card ul {
      list-style: none;
      padding: 0;
      display: flex;
      flex-wrap: wrap; }
    .page--detail-stepper .detail-stepper__card li {
      display: flex;
      margin-top: 8px; }
    .page--detail-stepper .detail-stepper__card--base li {
      width: 33%; }
    .page--detail-stepper .detail-stepper__card--base li div:first-child {
      width: 110px;
      height: 100%;
      flex-shrink: 0;
      color: #999; }
    .page--detail-stepper .detail-stepper__card--base li div:last-child {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: keep-all;
      width: calc(100% - 150px);
      line-height: 22px; }
    .page--detail-stepper .detail-stepper__card--stepper .hi-form {
      background: #f7fafe;
      padding: 24px 24px 0 24px; }
    .page--detail-stepper .detail-stepper__card--stepper .stepper {
      padding-top: 24px; }
    .page--detail-stepper .detail-stepper__card--stepper .hi-stepper__item {
      position: relative; }
    .page--detail-stepper .detail-stepper__card--stepper .hi-stepper__item--active::before {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      bottom: -24px;
      left: 30px;
      margin-left: -8px;
      border-style: solid;
      border-width: 0 12px 16px 12px;
      border-color: transparent transparent #f7fafe transparent; }
    .page--detail-stepper .detail-stepper__card--stepper .hi-stepper__item--done::before {
      display: none; }
    .page--detail-stepper .detail-stepper__card--stepper .form-title {
      font-size: 16px;
      justify-content: space-between;
      align-items: center;
      padding: 0 24px;
      height: 48px;
      background: #f7fafe;
      border-bottom: 1px solid rgba(66, 133, 244, 0.1); }
