.page--form-unfold-group {
  background: #fff;
  margin: 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--form-unfold-group .hi-form-item {
    padding: 0 24px; }
  .page--form-unfold-group .hi-form__title {
    font-size: 16px;
    height: 47px;
    line-height: 47px;
    padding: 0 24px;
    border-bottom: 1px solid #e7e7e7;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .page--form-unfold-group .hi-navmenu {
    margin: 0 24px; }
  .page--form-unfold-group .hi-navmenu__content {
    padding-top: 24px; }
  .page--form-unfold-group legend {
    color: #333;
    font-size: 16px; }
