.page--form-with-stepper {
  background: #fff;
  padding: 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--form-with-stepper .hi-form-item {
    padding: 0 24px; }
  .page--form-with-stepper .hi-form__title {
    display: flex;
    justify-content: space-between; }
  .page--form-with-stepper .stepper-container {
    width: 400px; }
  .page--form-with-stepper .photo-display {
    padding: 0; }
  .page--form-with-stepper legend {
    color: #333;
    font-size: 16px; }
