.page--detail-relevance {
  padding: 0 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--detail-relevance .detail-relevance__header .row-01 {
    color: #ccc;
    height: 32px;
    align-items: center;
    margin-bottom: 14px; }
  .page--detail-relevance .detail-relevance__header .row .spacer {
    margin: 0 10px; }
  .page--detail-relevance .detail-relevance__header .row-02 {
    color: #383e47;
    margin-top: 0;
    align-items: center;
    height: 52px;
    padding: 0;
    margin-bottom: 12px; }
  .page--detail-relevance .detail-relevance__header .row-02 h3 {
    margin: 0 0 4px 0; }
  .page--detail-relevance .detail-relevance__header .row-03 {
    padding-top: 4px;
    font-size: 12px;
    color: #999;
    margin-bottom: 18px; }
  .page--detail-relevance .detail-relevance__header .row-03 .spacer {
    margin: 0 20px;
    color: #e7e7e7; }
  .page--detail-relevance .detail-relevance__header .row-03 .spacer:last-child {
    display: none; }
  .page--detail-relevance .detail-relevance__card {
    background: #fff;
    padding: 0; }
    .page--detail-relevance .detail-relevance__card:not(:nth-child(2)) {
      margin-top: 24px; }
    .page--detail-relevance .detail-relevance__card .title {
      font-size: 16px;
      border-bottom: 1px solid #e7e7e7;
      padding: 0 24px 12px;
      margin: -12px -24px 0 -24px; }
      .page--detail-relevance .detail-relevance__card .title:not(:first-child) {
        margin-top: 32px; }
    .page--detail-relevance .detail-relevance__card .hi-navmenu__list {
      padding: 0 24px !important; }
    .page--detail-relevance .detail-relevance__card--base {
      list-style: none;
      padding: 0 24px 24px 24px; }
      .page--detail-relevance .detail-relevance__card--base li {
        display: flex;
        margin-top: 8px;
        line-height: 22px; }
      .page--detail-relevance .detail-relevance__card--base li div:first-child {
        width: 110px;
        height: 100%;
        flex-shrink: 0;
        color: #999; }
      .page--detail-relevance .detail-relevance__card--base li div:last-child {
        width: 68%; }
