.page--detail-double-column {
  padding: 0 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--detail-double-column .detail-double-column__header .row-01 {
    color: #ccc;
    height: 32px;
    align-items: center; }
  .page--detail-double-column .detail-double-column__header .row .spacer {
    margin: 0 10px; }
  .page--detail-double-column .detail-double-column__header .row-02 {
    color: #383e47;
    padding: 0 0 18px;
    margin-top: 0;
    align-items: center; }
  .page--detail-double-column .detail-double-column__header .row-02 h2 {
    margin: 4px 0; }
  .page--detail-double-column .detail-double-column__header .row-03 {
    padding-top: 4px;
    font-size: 12px;
    color: #999; }
  .page--detail-double-column .detail-double-column__header .row-03 .spacer {
    margin: 0 20px;
    color: #e7e7e7; }
  .page--detail-double-column .detail-double-column__header .row-03 .spacer:last-child {
    display: none; }
  .page--detail-double-column .detail-double-column__body {
    background: #fff;
    padding: 12px 24px; }
    .page--detail-double-column .detail-double-column__body .title {
      font-size: 16px;
      height: 48px;
      line-height: 48px; }
    .page--detail-double-column .detail-double-column__body .title:not(:first-child) {
      margin-top: 12px; }
    .page--detail-double-column .detail-double-column__body ul {
      list-style: none;
      padding: 0;
      display: flex;
      flex-wrap: wrap;
      margin: 0 0 12px 0; }
    .page--detail-double-column .detail-double-column__body li {
      display: flex;
      margin-top: 8px;
      width: 50%; }
    .page--detail-double-column .detail-double-column__body li div:first-child {
      width: 96px;
      height: 100%;
      flex-shrink: 0;
      color: #999; }
    .page--detail-double-column .detail-double-column__body li div:last-child {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: keep-all;
      width: 75%;
      line-height: 22px; }
