.page--workbench {
  padding: 16px 24px;
  min-height: calc(100vh - 64px);
  box-sizing: border-box; }
  .page--workbench .workbench--container {
    max-width: 1300px;
    margin: 0 auto; }
  .page--workbench .user {
    line-height: 36px;
    color: #333; }
    .page--workbench .user__icon {
      display: inline-block;
      width: 36px;
      height: 36px;
      margin-right: 12px;
      background: #fff;
      border: 1px solid #f6f6f6;
      border-radius: 50%;
      text-align: center; }
  .page--workbench .hi-grid__col-8 .card {
    height: 377px; }
    .page--workbench .hi-grid__col-8 .card .card__body {
      flex-basis: 377px;
      padding: 12px 24px 24px 24px; }
  .page--workbench .hi-grid__col-16 .card {
    height: 377px; }
    .page--workbench .hi-grid__col-16 .card .card__body {
      flex-basis: 377px; }
      .page--workbench .hi-grid__col-16 .card .card__body .hi-grid__row {
        width: 100%; }
      .page--workbench .hi-grid__col-16 .card .card__body .hi-timeline {
        margin-top: 14px;
        width: 100%; }
  .page--workbench .todo {
    width: 100%;
    color: #333; }
    .page--workbench .todo__item {
      padding: 9px 0; }
      .page--workbench .todo__item .approval-btn {
        color: #4285f4; }
  .page--workbench .hi-dropdown__title-text {
    padding-right: 4px; }
  .page--workbench .hi-stepper {
    padding: 24px 0 20px 0; }
